import Vue from 'vue'
import Router from 'vue-router'
import NProgress from 'nprogress';

import Login from './views/login.vue'
import createAccount from './views/createAccount';
//Dashboard Admin Components


import dashboard_admin from './routes/dashboard/admin/index';
import dashboard_student from './routes/dashboard/student/index';

//End Dashboard Admin Components


import HomePage from "./views/portifolio/home"
import pageNotFound from './views/portifolio/404.vue';
import store from './store'
import Auth from './auth';
Vue.use(Router)

const requireAuth = function(to, from, next){
  Auth.isAuthenticated().then(()=>{
    let path = to.path;
    if(to.name == "dashboard"){
      if(Auth.isAdmin()){
        next("/dashboard/admin")
      }else if(Auth.isStudent()){
        next("/dashboard/student")
      }
      return 1;
    }else if(path.indexOf("/dashboard/admin") !== -1 && Auth.isAdmin()){
      next(true);
    }else  if(path.indexOf("/dashboard/student") !== -1 && Auth.isStudent()){
      next(true);
    }else{
      next({path:'/login', query:{next: to.fullPath}});
    }
    
  }).catch(()=>{
    next({path:'/login', query:{next: to.fullPath}});
  })  
}
const noAuth = function(to, from, next){
  if(to.path =="/login" || to.path =="/create-account"){
    Auth.isAuthenticated().then(()=>{
      if(Auth.isAdmin()){
        next("/dashboard/admin")
      }else if(Auth.isStudent()){
        next("/dashboard/student")
      }
    }).catch(()=>{
      next();
    })  
  }else{
    next();
  }
}


export default new Router({
  linkExactActiveClass: "active",
  mode:"history",
  routes: [
    {
      path: '/',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path:'',
          component: HomePage,
          beforeEnter: noAuth,
        },
        {
          path: 'login',
          name: 'login',
          component: Login,
          beforeEnter: noAuth,
        },
        {
          path: 'create-account',
          name: 'account.create',
          component: createAccount,
          beforeEnter: noAuth,
        },        
        {
          path:'about-us/',
          component:  Login,
          name: 'about'
        },
        {
          path:'contact-us/',
          component:  Login,
          name: 'contact'
        },
      ]
    },
    {
      path: '/dashboard',
      component: {
        render (c) { return c('router-view') }
      },
      name: 'dashboard',
      beforeEnter: requireAuth,
      children: [
        {
          path: 'admin',
          component: {
            render(c) { return c('router-view')}
          },
          children : dashboard_admin.children
        },
        {
          path: 'student',
          component: {
            render(c) { return c('router-view')}
          },
          children : dashboard_student.children
        }
      ],

    },
    {
      path: '/logout',
      name: 'logout',
      component: Login,
      beforeEnter:(to, from, next)=>{
        store.state.token = null;
        localStorage.removeItem("token");
        next("/login");
      }
    },
    {
      path:"*",
      name: '404',
      component: pageNotFound
    }
  ]
})
