<template>
    <div class="bg-white" style="height:100vh;">
        <topNav />
 
            <div class="container " >
                <div class="row justify-content-center">
                    <div class="col-lg-5">
                        <card type="none" 
                            header-classes="bg-white pb-5"
                            body-classes="px-lg-5 py-lg-5"
                            class="border-0">
                            <template>
                                <div class="text-center text-muted mb-4">
                                    <small>Create in with us</small>
                                </div>
                                <form role="form" @submit="submit">
                                    <base-input 
                                                alternative
                                                :valid="telephoneValid"
                                                class="mb-3"
                                                placeholder="Telephone"
                                                v-model="telephone"
                                                addon-left-icon="fa fa-phone">
                                    </base-input>
                                    <base-input 
                                                alternative
                                                :valid="nameValid"
                                                class="mb-3"
                                                placeholder="Names"
                                                v-model="name"
                                                addon-left-icon="fa fa-user">
                                    </base-input>
                                    <base-input 
                                                alternative
                                                :valid="emailValid"
                                                class="mb-3"
                                                placeholder="E-mail"
                                                v-model="email"
                                                addon-left-icon="ni ni-email-83">
                                    </base-input>

                                    <base-input 
                                                alternative
                                                :valid="passwordValid"
                                                type="password"
                                                placeholder="PIN"
                                                v-model="password"
                                                addon-left-icon="ni ni-lock-circle-open">
                                    </base-input>
                                    <base-input 
                                                alternative
                                                :valid="comPasswordValid"
                                                type="password"
                                                placeholder="Repeat PIN"
                                                v-model="comPassword"
                                                addon-left-icon="ni ni-lock-circle-open">
                                    </base-input>
                                    <div class="text-center">
                                          <button type="submit" class="btn btn-primary my-4 my-btn">Create Account</button>
                                    </div>
                                    <div v-if="error=='incorrect'">
                                        <base-alert type="warning" dismissible class="text-center">
                                            <strong>Warning!</strong> Email/Password are incorrect!
                                        </base-alert>
                                    </div>
                                    <div class="col-12 text-right">
                                        <router-link
                                            class="text-light"
                                            to="/login">
                                            <small>Already has account!</small>
                                        </router-link>
                                    </div>
                                </form>
                            </template>
                        </card>
                        <div class="row mt-0">
                            
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>

/* eslint-disable */
import axios from 'axios';
import Auth from '../auth';

import topNav from './portifolio/includes/nav'
export default {
  components:{
      topNav
  },
  name:'login',
  data(){
      return {
          email : '',
          password : '',
          telephone: '',
          name: '',
          error: '',
          emailValid: null,
          passwordValid: null,
          comPassword: '',
          comPasswordValid: null,
          telephoneValid: null,
          nameValid: null,
      }
  },
  created(){
  },
  methods:{
      submit : function(ele){
          ele.preventDefault();
          this.error = "";
          this.validateForm();
          let credentials = {
              email : this.email,
              password : this.password,
              phone: this.telephone,
              name: this.name
          }
          if(!this.passwordValid || ! this.emailValid){
              return;
          }
          let loader = this.$loading.show({
                container: null,
                canCancel: false,
                loader: "dots",
          });

          axios({
                method:'POST',
                data: credentials,
                url: this.$store.state.serverAddress+'/students/create-account',
            }).then(result=>{
                let data = result.data;
                if(data.status == "ok"){
                    let token = data.token;
                    localStorage.setItem("token", token);
                    this.$store.state.token = token;
                    this.$store.state.activeUser = data.user;
                    if(this.$route.query.next != undefined){
                        this.$router.push(this.$route.query.next);
                    }else{
                        this.$router.push("/dashboard/student")
                    }
                }else{
                    this.error = "incorrect";
                }
                loader.hide()
            }).catch(error=>{
                loader.hide()
                console.warn(error);
            })
      },
      validateForm: function(){
          if(this.email.length < 1){
              this.emailValid = false;
          }else{
              this.emailValid = true;
          }
          if(this.name.length < 1){
              this.nameValid = false;
          }else{
              this.nameValid = true;
          }
          if(this.telephone.length < 1){
              this.telephoneValid = false;
          }else{
              this.telephoneValid = true;
          }

          if(this.password !== this.comPassword){
              this.passwordValid = false;
              this.comPasswordValid = false;
          }else{
              this.passwordValid = true;
              this.comPasswordValid = true;
          }

          if(this.password.length < 1){
              this.passwordValid = false;
          }else{
              this.passwordValid = true;
          }

          
      }
  }
}
</script>
<style  scoped>
    .bg-login-image{
        background:transparent;
    }
    .my-btn{
        background-color: #f96332 !important;
        border: none;
    }
    
</style>
