const questions_create =() => import('../../../views/dashboard/admin/questions/create');
const questions_view =() => import( '../../../views/dashboard/admin/questions/view');
import store from "../../../store";
import axios from 'axios'
import NProgress from 'nprogress';



export default {
    children: [
        {
          path:'',
          component: questions_view,
          name: 'questions.view',
          beforeEnter: (to, from, next)=>{
            NProgress.start()
            if(store.state.serverQuestions.length > 0){
              next(true)
              NProgress.done();
            }else{
              axios({
                  url: store.state.serverAddress +`/questions/`,
                  method: 'GET',
                  data : null,
                  headers:{
                      Authorization : store.state.token,
                  }
              }).then((result)=>{
                  result.data = result.data.trim();
                  result.data = JSON.parse("["+result.data.substring(0, result.data.length -1)+"]");
                  let arrayData = [];
                  result.data.forEach((data, index)=>{
                    arrayData = arrayData.concat(data);
                    result.data[index] = null;
                  })
                  result.data = [];

                  store.state.serverQuestions = arrayData;
                  next();
              }).catch(error=>{
                  console.log(error);
                  next(false);
              })
            }
            
            
          }
        },
        {
          path:'create/',
          component: questions_create,
          name: 'questions.create'
        },
    ]
}