<template>
    <div>
        <topNavBar />
        <leftSideBar />
        <div class="content">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-12">
                        <h2>{{scoreMarks}} /20</h2>
                    </div>
                    <div class="col-md-12">
                        <b-card no-body class="mb-1" v-for="(examReview, indexTop) in examReviews" :key="indexTop">
                            <b-card-header header-tag="header" class="p-1" role="tab" v-b-toggle="'accordion-' + indexTop" :class="{
                                'bg-success': examReview.isItCorrect && exam_submited,
                                'bg-warning': !examReview.isItCorrect && exam_submited
                                }" style="cursor: pointer;">
                                
                                    <span  style="word-break: break-all;"  :class="{
                                        'text-primary' : !exam_submited,
                                        'text-white' : exam_submited,
                                    }" >{{ (indexTop+1)+") " +examReview.title}}</span> 
                                
                            </b-card-header>
                            <b-collapse :id="`accordion-${indexTop}`"  accordion="my-accordion" role="tabpanel">
                                <b-card-body>
                                    <b-card-text :set="choices = JSON.parse(examReview.choices)">
                                        <ul style="list-style:none !important;">
                                            <b-form-group :set="shuffledChoices = shuffleArray(choices)">
                                                <div  v-for="(choice, index) in shuffledChoices" style="font-weight:bold;font-size:13px;color:black;opacity:0.9;" :key="choice.id">
                                                    <div style="padding-left:1px;margin-bottom:1px;background:;" class="choiceSelect">
                                                        <b-form-radio 
                                                            :model="1"
                                                            name="some-radios" 
                                                            value= "1"
                                                            style="padding-top:4px;visibility:hidden;"
                                                        
                                                        >
                                                        </b-form-radio>
                                                        <li style="margin-left:20px;padding-top:5px;padding-bottom:5px;width:95% !important;margin-top:-25px;margin-bottom: 5px;padding-left: 10px;" class="choiceOption">
                                                            <span
                                                                :class="{
                                                                    'text-success': (choice.id == examReview.correctAnswer),
                                                                    'text-danger' : (choice.id == examReview.student_answer) && (choice.id !== examReview.correctAnswer),
                                                                }"
                                                                :style="{
                                                                    'text-decoration': ((choice.id == examReview.student_answer) && (choice.id !== examReview.correctAnswer)) ? 'line-through' : 'none',
                                                                }"
                                                                v-if="choice.answer.indexOf('<img') == -1"> 
                                                                <span>{{ index+1 }})</span>
                                                                {{ choice.answer}} 
                                                            </span>


                                                            <span
                                                                :class="{
                                                                    'text-success': (choice.id == examReview.student_answer),
                                                                    'bg-success': (choice.id == examReview.correctAnswer),
                                                                    'bg-danger' : ((choice.id == examReview.student_answer) && (choice.id !== examReview.correctAnswer)) ? 1 : 0,
                                                                }"
                                                                v-if="choice.answer.indexOf('<img') !== -1">
                                                                <span>{{ index+1 }})</span>
                                                                <span v-html="choice.answer"></span>
                                                            </span>
                                                        </li>
                                                    </div>
                                                </div>
                                            </b-form-group>
                                        </ul>
                                    </b-card-text>
                                </b-card-body>
                            </b-collapse>
                        </b-card>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>
<script>
import topNavBar from '../includes/topNavBar';
import leftSideBar from '../includes/leftSideBar';
import axios from 'axios';
export default {
    name:'student',
    components:{
        topNavBar,
        leftSideBar
    },
    data(){
        return {
            exam : [],
            totalQuestions: 0,
            totalPassQuestions: 0,
            examReviews: {},
            scoreMarks : 0,
            exam_submited: false,
            student: this.$store.state.activeUser

            
        }
    },
    created(){
        this.retriveExam()
    },
    methods:{
        shuffleArray: function(array) {
            // for (var i = array.length - 1; i > 0; i--) {
            //     var j = Math.floor(Math.random() * (i + 1));
            //     var temp = array[i];
            //     array[i] = array[j];
            //     array[j] = temp;
            // }
            return array;
        },
        retriveExam: function(){
            let student_id = this.student.id;
            let exam_id = this.$route.params.exam_id;
            axios({
                url: this.$store.state.serverAddress +`/student/${student_id}/exams/${exam_id}/review`,
                method: 'GET',
                data : null,
                headers:{
                    Authorization : this.$store.state.token,
                }
            }).then((result)=>{
                this.examReviews = result.data.review;
                this.scoreMarks = result.data.marks;
                this.exam_submited = result.data.submited;
                
            }).catch(error=>{
                console.log(error);
            })
        }
    }
    
}
</script>
<style>
    body{
        background: #FAFAFA !important;
    }
    span.text-success span img{
        border: 5px solid green;
    }
</style>
<style scoped lang='scss'>
    
    .content{
        top: 100px;
        background: white;
        position: absolute;
        left: 100px;
        box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.06);
        margin-bottom: 50px;
        width: calc(100% - 150px);


    }
    h4{
        font-weight: bold;
        font-size: 17px;
        color: rgb(129, 5, 216);
    }


    table{
        padding-top: 50px;
        border: none;
        thead{
            border: none;
            th{
                border: none;
                font-size: 12px;
                opacity: 0.5;
                padding: 5px;
            }
        }

        tbody{
            border: none;
            tr:nth-child(even), tr:hover{
                background-color: rgba(211, 211, 211, 0.123);
            }
            
            tr{
                border: none;
                border-top: 1px solid lightgray;
                
                td{
                    border: none;
                    font-size: 13px;
                    
                    padding: 5px;

                    i{
                        font-size: 16px;
                    }

                    span{
                        font-size: 12px;
                    }
                }
            }
        }
    }

    ol {
        counter-reset: list;
        margin: 0;
    }

    ol >  div div .b-form-radio  li {
        list-style: none;
        position: relative;
    }

    ol > div div .b-form-radio li:before {
        counter-increment: list;
        content: counter(list, lower-alpha) ") ";
        position: absolute;
        left: -1.4em;
    }
    .choiceSelect:hover div{
        display:block !important;
    }
    li span span img{
        height: 40px !important;
        width: 50px !important;
    }
</style>