
const AdminHome = () => import('../../../views/dashboard/admin/home');
const customers_view = () => import('../../../views/dashboard/admin/customers/view');
const examinations_view = () => import('../../../views/dashboard/admin/examninations/view');
const examinations_create = () => import('../../../views/dashboard/admin/examninations/create');
const commissions_view = () => import('../../../views/dashboard/admin/commissions/view');

const customers_create = () => import('../../../views/dashboard/admin/customers/create');
import questions  from './questions';

const students_view = () => import('../../../views/dashboard/admin/students/view');
const students_create = () => import('../../../views/dashboard/admin/students/create');
import store from "../../../store";
import axios from 'axios';
import NProgress from 'nprogress';

export default {
    children:[
        {
          path:'',
          component: AdminHome,
          name: 'dashboard.admin'
        },
        {
          path:'questions/',
          component: {
            render (c) { return c('router-view') }
          },
          children: questions.children
        },
        {
          path:'customers/',
          component: {
            render (c) { return c('router-view') }
          },
          children:[
            {
              path:'',
              component: customers_view,
              name: 'customers.view'
            },
            {
              path:'create/',
              component: customers_create,
              name: 'customers.create'
            },
          ]
        },
        {
          path:'commissions/',
          component: {
            render (c) { return c('router-view') }
          },
          children:[
            {
              path:'',
              component: commissions_view,
              name: 'commissions.view'
            },
            {
              path:'create/',
              component: commissions_view,
              name: 'commissions.create'
            },
          ]
        },
        {
          path:'students/',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path:'',
              name: 'students_view',
              component: students_view,
              beforeEnter: (to, from, next)=>{
                NProgress.start()
                if(store.state.serverStudents.length > 0){
                  next(true)
                  NProgress.done();
                }else{
                  axios({
                      url: store.state.serverAddress+`/students/list`,
                      method: 'GET',
                      data : null,
                      headers:{
                          Authorization : store.state.token,
                      }
                  }).then((result)=>{
                      store.state.serverStudents = result.data.students;
                      console.log(result.data.students)
                      next();
                  }).catch(error=>{
                      console.log(error);
                      next(false);
                  })
                }
                
                
              }
            },
            {
              path: 'create/',
              name:'student_create',
              component: students_create,
            }
          ]
        },
        {
          path:'examinations/',
          component: {
            render (c) { return c('router-view') }
          },
          children:[
            {
              path:'',
              component: examinations_view,
              name: 'examaninations.view',
              beforeEnter: (to, from, next)=>{
                NProgress.start()
                if(store.state.serverExams.length > 0){
                  next(true)
                  NProgress.done();
                }else{
                  axios({
                      url: store.state.serverAddress+`/exams/list`,
                      method: 'GET',
                      data : null,
                      headers:{
                          Authorization : store.state.token,
                      }
                  }).then((result)=>{
                      store.state.serverExams = result.data.exam;
                      next();
                  }).catch(error=>{
                      console.log(error);
                      next(false);
                  })
                }
                
                
              }
            },
            {
              path:'create/',
              component: examinations_create,
              name: 'examinations.create',
              beforeEnter: (to, from, next)=>{
                NProgress.start()
                if(store.state.serverQuestions.length > 0){
                  next(true)
                  NProgress.done();
                }else{
                  axios({
                      url: store.state.serverAddress +`/questions/`,
                      method: 'GET',
                      data : null,
                      headers:{
                          Authorization : store.state.token,
                      }
                  }).then((result)=>{
                      result.data = result.data.trim();
                      result.data = JSON.parse("["+result.data.substring(0, result.data.length -1)+"]");
                      let arrayData = [];
                      result.data.forEach((data, index)=>{
                        arrayData = arrayData.concat(data);
                        result.data[index] = null;
                      })
                      result.data = [];

                      store.state.serverQuestions = arrayData;
                      next();
                  }).catch(error=>{
                      console.log(error);
                      next(false);
                  })
                }
                
                
              }
            },
          ]
        }
    ]
}