
import studentHome from '../../../views/dashboard/student/home';
import studentExamReview from '../../../views/dashboard/student/exams/review';
import studentExamWorkPlace from '../../../views/dashboard/student/exams/workplace';
import studentProfile from '../../../views/dashboard/student/profile';

import store from "../../../store";
import axios from 'axios';
import NProgress from 'nprogress';

export default {
    children: [
        {
            path : '',
            component : {
                render(c) { return c('router-view')}
            },
            children : [
                {
                    name: 'dashboard.student.home',
                    path: '',
                    component: studentHome,
                },
                {
                    name: 'dashboard.student.exam.review',
                    path: 'exams/:exam_id/review/',
                    component: studentExamReview,
                },
                {
                    name: 'dashboard.student.exam.workplace',
                    path: 'exams/:exam_id/workplace/',
                    component: studentExamWorkPlace,
                },
                {
                    name: 'dashboard.student.profile',
                    path: 'profile',
                    component: studentProfile,
                }
            ]
        }
    ]
}