<template>
    <div class="bg-white" style="height:100vh;">
        <topNav />
 
            <div class="container  pt-lg-md" >
                <div class="row justify-content-center">
                    <div class="col-lg-5">
                        <card type="none" 
                            header-classes="bg-white pb-5"
                            body-classes="px-lg-5 py-lg-5"
                            class="border-0">
                            <template>
                                <div class="text-center text-muted mb-4">
                                    <small>Sign in with us</small>
                                </div>
                                <form role="form" @submit="submit">
                                    <base-input 
                                                alternative
                                                :valid="emailValid"
                                                class="mb-3"
                                                placeholder="E-mail / Telephone"
                                                v-model="email"
                                                addon-left-icon="ni ni-email-83">
                                    </base-input>
                                    <base-input 
                                                alternative
                                                :valid="passwordValid"
                                                type="password"
                                                placeholder="Password / PIN"
                                                v-model="password"
                                                addon-left-icon="ni ni-lock-circle-open">
                                    </base-input>
                                    <base-checkbox type='success'>
                                        Remember me
                                    </base-checkbox>
                                    <div class="text-center">
                                          <button type="submit" class="btn btn-primary my-4 my-btn">Sign In</button>
                                    </div>
                                    <div v-if="error=='incorrect'">
                                        <base-alert type="warning" dismissible class="text-center">
                                            <strong>Warning!</strong> Email/Password are incorrect!
                                        </base-alert>
                                    </div>
                                </form>
                            </template>
                        </card>
                        <div class="row mt-3">
                            <div class="col-6">
                                <router-link
                                     class="text-light"
                                     to="/forget-password">
                                     <small>Forgot password?</small>
                                </router-link>
                            </div>
                            <div class="col-6 text-right">
                                <router-link
                                     class="text-light"
                                     to="/create-account">
                                     <small>Create new account</small>
                                </router-link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>

/* eslint-disable */
import axios from 'axios';
import Auth from '../auth';

import topNav from './portifolio/includes/nav'
export default {
  components:{
      topNav
  },
  name:'login',
  data(){
      return {
          email : '',
          password : '',
          error: '',
          emailValid: null,
          passwordValid: null,
      }
  },
  created(){
  },
  methods:{
      submit : function(ele){
          ele.preventDefault();
          this.error = "";
          if(this.email.length < 1){
              this.emailValid = false;
          }else{
              this.emailValid = true;
          }

          if(this.password.length < 1){
              this.passwordValid = false;
          }else{
              this.passwordValid = true;
          }
          let credentials = {
              email : this.email,
              password : this.password
          }
          if(!this.passwordValid || ! this.emailValid){
              return;
          }
          let loader = this.$loading.show({
                // Optional parameters
                container: null,
                canCancel: false,
                loader: "dots",
                
            });
          axios({
                method:'POST',
                data: credentials,
                url: this.$store.state.serverAddress +"/login",
            }).then(result=>{
                let data = result.data;
                if(data.status == "ok"){
                    let token = data.token;
                    localStorage.setItem("token", token);
                    this.$store.state.token = token;
                    this.$store.state.activeUser = data.user;
                    if(this.$route.query.next != undefined){
                        this.$router.push(this.$route.query.next);
                    }else{
                        if(Auth.isAdmin()){
                            this.$router.push("/dashboard/admin")
                        }else if(Auth.isStudent()){
                            this.$router.push("/dashboard/student")
                        }
                    }
                }else{
                    this.error = "incorrect";
                }
                loader.hide()
            }).catch(error=>{
                loader.hide()
                console.warn(error);
            })
      }
  }
}
</script>
<style  scoped>
    .bg-login-image{
        background:transparent;
    }
    .my-btn{
        background-color: #f96332 !important;
        border: none;
    }
    
</style>
