<template>
    <div>
        <topNavBar />
        <leftSideBar />
        <div class="content">
            <div class="container-fluid">
                <div class="row" >
                    <div class="col-md-12" >
                        <h4>Student Profile</h4>
                        <div class="col-md-12 Datatables pt-2 ">
                            <form>
                                <div class="row">
                                    <div class="col-md-12">
                                        <b-card-img :src="previewImage" style="height:80px;width:80px;">
                                        </b-card-img>
                                        
                                        <b-button @click="$refs.file.click()" variant="default" size="sm" class="ml-4" >Upload New Image</b-button>
                                        <input ref="file" type="file" name="image" style="display:none" @change="picChange"  />
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-4">
                                        <p>Names</p>
                                        <base-input alternative type="text" v-model="name" placeholder="Fullnames"></base-input>
                                    </div>
                                    <div class="col-md-4">
                                        <p>Username</p>
                                        <base-input alternative type="text" v-model="username" placeholder="Username"></base-input>
                                    </div>
                                    <div class="col-md-4">
                                        <p>E-Mail</p>
                                        <base-input alternative v-model="email" placeholder="name@example.com"></base-input>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-6">
                                        <p >Phone</p>
                                        <base-input alternative type="text" v-model="phone" placeholder="Telephone" addon-left-icon="ni ni-zoom-split-in"></base-input>
                                    </div>
                                    <div class="col-md-6">
                                        <p >Whatsapp Number</p>
                                        <base-input alternative type="text" v-model="whatsapp" placeholder="Whatsapp" addon-right-icon="ni ni-zoom-split-in"></base-input>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-4">
                                        <p>Current Password</p>
                                        <base-input alternative type="password" :valid="oldPasswordValid" v-model="currentPassword" placeholder="Current Password"></base-input>
                                    </div>
                                    <div class="col-md-4">
                                        <p>New Password</p>
                                        <base-input alternative type="password" :valid="newPasswordValid" v-model="newPassword" placeholder="New Password"></base-input>
                                    </div>
                                    <div class="col-md-4">
                                        <p>Comfirm Password</p>
                                        <base-input alternative type="password" :valid="comPasswordValid" v-model="comPassword" placeholder="Comfirm Password"></base-input>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-6">
                                        <base-button outline type="default" @click="saveStudent">UPDATE PROFILE</base-button>
                                    </div>
                                </div>
                            </form>
                            
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>
<script>
import topNavBar from './includes/topNavBar';
import leftSideBar from './includes/leftSideBar';
import axios from 'axios';
export default {
    name:'student',
    components:{
        topNavBar,
        leftSideBar
    },
    data(){
        return {
            student: this.$store.state.activeUser,
            email:'',
            phone: '',
            whatsapp: '',
            name: '',
            username: '',
            code: this.$store.state.activeUser.code,
            profilePic : null,
            previewImage: "",
            currentPassword: null,
            newPassword: null,
            comPassword: null,
            oldPasswordValid: null,
            newPasswordValid: null,
            comPasswordValid: null,

        }
    },
    created(){
        this.initializeData();
    },
    methods:{
        initializeData: function(){
            this.email = this.student.email;
            this.phone = this.student.phone;
            this.whatsapp = this.student.whatsapp;
            this.name = this.student.name;
            this.username = this.student.username;
            this.code = this.student.code;
            this.previewImage = this.$store.state.serverAddress.replace("api","")+"storage"+this.student.profile_image;


        },
        saveStudent:function(){
            if((this.newPassword !== this.comPassword) && (this.currentPassword != null && this.currentPassword.trim() != "" )){
                this.newPasswordValid = false;
                this.comPasswordValid = false;
                return;
            }else{
                if(this.newPassword != null && this.newPassword.trim() != ""){
                    this.newPasswordValid = true;
                    this.comPasswordValid = true;
                }
                
            }
            let data = new FormData();
            if(this.profilePic !== null){
                data.append('picture', this.profilePic); 
            }
            data.append('name', this.name);
            data.append('email', this.email);
            data.append('phone', this.phone);
            data.append('whatsapp', this.whatsapp);
            data.append("username", this.username);
            if(this.currentPassword != null && this.currentPassword.trim() != ""  ){
                data.append('currentPassword', this.currentPassword);
                data.append('newPassword', this.newPassword);
            }
            
            this.$snotify.async('Please while we examine the student', 'Registering', () => new Promise((resolve, reject) => {
                axios({
                    url: this.$store.state.serverAddress+'/students/profile/update',
                    method: 'POST',
                    data : data,
                    headers: {
                        Authorization: this.$store.state.token,
                        "Content-type": "multipart/form-data"
                    }
                }).then(result=>{
                    if(result.data.status == 200){
                        this.$store.state.activeUser = result.data.student;
                        resolve({
                            title: 'Success',
                            body: 'Profile successfully updates',
                            timeout: 2000,
                            config: {
                                closeOnClick: true,
                                timeout: 2000,
                            }
                        });
                    }else{
                        this.oldPasswordValid = false;
                        reject({
                            title: 'Error!!',
                            body: result.data.message,
                            timeout: 2000,
                            config: {
                                closeOnClick: true,
                                timeout: 2000,
                            }
                        });
                    }
                    
                }).catch(error=>{
                    reject({
                        title: 'Error!!',
                        body: 'Error occured while updating student Profile',
                        timeout: 2000,
                        config: {
                            closeOnClick: true,
                            timeout: 2000,
                        }
                    });
                })
            })
            )
            
        },
        picChange: function(event){
            this.profilePic = event.target.files[0]
            const reader = new FileReader();
            reader.readAsDataURL(this.profilePic);
            reader.onload = e =>{
                this.previewImage = e.target.result;
            };
        }
    }
    
}
</script>
<style>
    body{
        background: #FAFAFA !important;
    }
    .modal-backdrop{
        background-color: #9c27b02e !important;
    }
</style>
<style scoped lang='scss'>
    .content{
        top: 100px;
        // background: white;
        position: absolute;
        left: 100px;
        // box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.06);
        margin-bottom: 50px;
        width: calc(100% - 150px);
    }
    .Datatables{
        background: white;
        box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.06);
        padding-bottom: 20px;
    }
    h4{
        font-weight: bold;
        font-size: 17px;
        color: rgb(129, 5, 216);
    }
</style>