<script>
import topNavBar from './includes/topNavBar';
import leftSideBar from './includes/leftSideBar';
import axios from 'axios';
export default {
    name:'student',
    components:{
        topNavBar,
        leftSideBar
    },
    data(){
        return {
            exams : this.$store.state.studentExams,
            totalExams : 0,
            totalPassExams : 0,
            totalExercises : 0,
            totalPassExercises: 0,
            activeExam: null,
            student: this.$store.state.activeUser,
            profileImage : "",
        }
    },
    created(){
        this.retriveExams();
        this.profileImage = this.$store.state.serverAddress.replace("api","")+"storage"+this.student.profile_image;
    },
    methods:{
        retriveExams: function(){
            let student_id = this.student.id;
            axios({
                url: this.$store.state.serverAddress +`/student/${student_id}/exams`,
                method: 'GET',
                data : null,
                headers:{
                    Authorization : this.$store.state.token,
                }
            }).then((result)=>{
                this.$store.state.studentExams = result.data.exams;
                this.exams = result.data.exams.slice(0,5);
                this.totalExams = result.data.exams.length;
                this.totalPassExams = result.data.exams.filter((exam)=>{
                    return exam.resultsLevel.toLowerCase()=='pass';
                }).length;
                let activeEx = this.exams.find((exam)=>{
                    return !exam.attend;
                });
                if(this.activeEx != undefined){
                    this.activeExam = this.activeEx.id;
                }
            
            }).catch(error=>{
                console.log(error);
            })
        }
    }
    
}
</script>
